.header-top-wrapper{

    overflow: hidden;
    position: relative;
    z-index: 1000;
    height: 40px;
    background: #B5B35C;

  // position: relative;
  .splide__slide{ 
    margin-top: 6px;
    p{
      margin-bottom: 3px;
      margin-left: 8px;
      font-family: Jost;
      font-weight: 400;
    }
    .top-bar-svg{
      img{
        vertical-align: top;
        
      }
    }
  }
}

.logo{
  margin-right: 24px;
}


// main -herader- search 
.search-from{
  
  height: 40px;
  @include breakpoint(large){
    width: 351px;
  }

.search-form {
  .form-group {
    position: relative;
    margin-bottom: 0;
    #s {
    
      padding: 9px 15px;
      border: 1px solid #000000;
      border-radius: 4px;
      background: #F8F8F8;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    .form-control:focus {
      color: #495057;
      background-color: #fff;
      border-color: #80bdff;
      outline: 0;
      box-shadow: none;
  }
  }
  .icon {
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
  }
}
}
.main-menu{

  ul{
    li{
      display: inline-block;
      margin-right: 32px;
     
      a{
        font-size: 20px;
        font-family: "Jost";
        font-weight: 400;
        text-transform: uppercase;
        color: #000000;
        text-decoration: none;
        display: block;
        height: 100%;
        padding: 37px 0;
        p{
          font-size: 20px;
          font-family: $jostfamily;
          font-weight: 400;
          text-transform: uppercase;
          color: $current-Black;
        }
        .menu-arrow{
          width: 24px;
        height: 24px;
         img{
          display: inline-block;
          vertical-align: middle;
         }
        }
      }
      &:last-child {
        margin-right: 0;
      }
    }
    
  }
}

.user-cart-navigation{
  margin-left: 48px;
  ul{
    li{
      a{
        padding: 10px;
      }
    }
  }
}



.main-header-wrapper{
 
  border-top: $border-top;
  border-bottom: $border-bottom;
   position: relative;
  height: 152px;
// @include breakpoint(medium){
 
// }
@include breakpoint(large){
  height: 104px;
}

.submenu{
  padding:0;
  text-align: center;
   width: 100%;
  position: absolute;
  top: 60px;
  left: 0;


}}
// main -herader- search end

.main-header-wrapper {
.submenu{
  opacity: 0;
  visibility: hidden;
  padding:0;
  text-align: center;
   width: 100%;
  position: absolute;
  top: 100%;


  background: #fff;
  transition: 0.3s all ease-in-out;
 
}

.singel-menu{
  padding: 10px 0;
  
  ul{
    li{
      text-align: left;
      display: block;
      
      a{
        font-family: 'GT Super Txt Trial';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        margin: 24px;
        padding: 0;
        color: #000000;
      }
    }
  }
  
}
}

.main-menu ul li:hover > .submenu{
 opacity: 1;
 visibility: visible;
 top: 101%; 
 z-index: 1000;
}

.list-unstyled.components{

    li{
      display: block;
      a{
        font-family: 'Jost';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        text-transform: uppercase;
        color: #000000;

      }
      ul{
        li{
          a{
            color: #000000;
            font-family: 'GT Super Txt Trial';
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
          }
        }
      }
    }
  
}

#sidebar {
  color: #000;
  background: #fff;
}
#sidebar.active {
  left: 0;
  right: 0;
  width: 100%;
}



.sider-bar-top{
 
  padding: 0 24px 0 16px;
  .sidebar-header-logo{
    margin: 38px 0 32px 0;
  
  }
}

#sidebar ul li {
  padding: 0 24px ;
  border-top: $border-top;
  border-top: $border-bottom;
  a:hover{
    color: #000;}
    ul{
      margin-bottom: 10px;
      li{
        border: none;
      }
    }
}


.m-rotate{
      transform: rotate(180deg);
    }



.list-unstyled.navigation{
 ul{
  li{

    display: block;
  }
 }
}

.crote{
  transition: 0.3s all ease-in-out;
}

